<template>
  <div>
    <!-- Show My Location Button -->
    <button v-if="!showMap" @click="handleShowMap" class="show-map-btn">
      Show My Location
    </button>

    <div v-if="showMap">
      <!-- Loading Message -->
      <div v-if="loading">Loading map...</div>

      <!-- Map Container -->
      <div id="map" ref="mapContainer" style="height: 500px; width: 100%"></div>

      <!-- Display User Location -->
      <div v-if="location">
        <h3>Your Current Location</h3>
        <p>Latitude: {{ location.lat.toFixed(6) }}</p>
        <p>Longitude: {{ location.lng.toFixed(6) }}</p>
      </div>

      <!-- Display Error Message -->
      <p v-if="error" class="error">{{ error }}</p>
    </div>
  </div>
</template>

<script>
import L from "leaflet";
import "leaflet/dist/leaflet.css";
import axios from "axios";

export default {
  name: "MapComponent",
  data() {
    return {
      map: null,
      marker: null,
      circle: null,
      location: null,
      error: null,
      loading: false,
      showMap: false,
    };
  },
  methods: {
    async handleShowMap() {
  this.showMap = true;
  await this.$nextTick(); // Ensures the DOM updates before running the functions
  this.initMap();
  this.getCurrentLocation();
  window.addEventListener("resize", this.handleResize);
    },

    initMap() {
      if (!this.$refs.mapContainer) {
        this.error = "Map container not found!";
        return;
      }

      this.map = L.map(this.$refs.mapContainer).setView([51.505, -0.09], 13);

      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        attribution: ' <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>',
      }).addTo(this.map);
    },

    getCurrentLocation() {
      if (!navigator.geolocation) {
        this.error = "Geolocation is not supported by your browser.";
        return;
      }

      this.loading = true;
      navigator.geolocation.getCurrentPosition(
        async (position) => {
          this.location = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
          };

          this.updateMapLocation();
          this.loading = false;

          // Automatically mark attendance
          await this.markAttendance();
        },
        (error) => {
          this.error = "Unable to get location: " + error.message;
          this.loading = false;
        },
        { timeout: 10000, enableHighAccuracy: true }
      );
    },

    updateMapLocation() {
      if (!this.map) return;

      this.map.setView([this.location.lat, this.location.lng], 15);

      if (this.marker) {
        this.marker.setLatLng([this.location.lat, this.location.lng]);
      } else {
        this.marker = L.marker([this.location.lat, this.location.lng])
          .addTo(this.map)
          .bindPopup("You are here!")
          .openPopup();
      }

      // Draw a 500m radius
      if (this.circle) {
        this.circle.setLatLng([this.location.lat, this.location.lng]);
      } else {
        this.circle = L.circle([this.location.lat, this.location.lng], {
          color: "blue",
          fillColor: "#3F51B5",
          fillOpacity: 0.1,
          radius: 500, // 500 meters
        }).addTo(this.map);
      }
    },

    async markAttendance() {
      const volunteerId = localStorage.getItem("volunteer_id"); // Retrieve volunteer ID from localStorage

      if (!volunteerId) {
        this.error = "Volunteer ID not found. Please log in.";
        return;
      }

      const attendanceData = {
        volunteer_id: volunteerId,
        location_latitude: this.location.lat,
        location_longitude: this.location.lng,
      };

      try {
        const response = await axios.post("http://localhost:3000/volunteer/mark-attendance", attendanceData);
        console.log("Attendance Marked:", response.data);
      } catch (error) {
        console.error("Error marking attendance:", error);
        this.error = "Error marking attendance. Please try again.";
      }
    },

    handleResize() {
      if (this.map) {
        this.map.invalidateSize();
      }
    },
  },
  beforeUnmount() {
    window.removeEventListener("resize", this.handleResize);

    if (this.map) {
      this.map.stop();
      this.map.off();
      this.map.remove();
    }
  },
};
</script>

<style>
.error {
  color: red;
}

.show-map-btn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-bottom: 10px;
}

.show-map-btn:hover {
  background-color: #45a049;
}

#map {
  height: 500px;
  width: 100%;
}

.leaflet-container {
  width: 100%;
  height: 500px !important;
}
</style>
