<template>
  <div>
    <!-- Loading Spinner -->
    <div v-if="loading">
      <p>Loading attendance records...</p>
      <div class="spinner"></div>
    </div>

    <!-- Attendance Records Table -->
    <div v-else>
      <!-- If attendance records exist -->
      <div v-if="attendance.length">
        <h2>Volunteer Attendance Records</h2>
<table border="1">
  <thead>
    <tr>
      <th>Attendance Date</th>
      <th>Location Latitude</th>
      <th>Location Longitude</th>
      <th>Start Time</th>
      <th>End Time</th>
    </tr>
  </thead>
  <tbody>
    <!-- Loop through attendance records -->
    <tr v-for="record in attendance" :key="record.volunteer_attendance_id">
      <td>{{ new Date(record.attendance_date).toLocaleString() }}</td>
      <td>{{ record.location_latitude }}</td>
      <td>{{ record.location_longitude }}</td>
      <td>{{ new Date(record.start_time).toLocaleString() }}</td>
      <td>{{ new Date(record.end_time).toLocaleString() }}</td>
    </tr>
  </tbody>
</table>

      </div>

      <!-- No records found -->
      <div v-else>
        <p>No attendance records found.</p>
      </div>

      <!-- Error Message Section -->
      <div v-if="error" class="error">
        <p>{{ error }}</p>
      </div>

      <!-- Add Attendance Form -->
      <h3>Add Attendance</h3>
      <form @submit.prevent="addAttendance">
        <div>
          <label for="event_name">Event Name:</label>
          <input type="text" id="event_name" v-model="newAttendance.event_name" required />
        </div>
        <div>
          <label for="attendance_status">Attendance Status:</label>
          <select id="attendance_status" v-model="newAttendance.attendance_status" required>
            <option value="attended">Attended</option>
            <option value="missed">Missed</option>
            <option value="late">Late</option>
          </select>
        </div>
        <button type="submit">Add Attendance</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'VolunteerAttendance',

  data() {
    return {
      attendance: [],  // Array to store fetched attendance records
      newAttendance: {
        event_name: '',
        attendance_status: 'attended',
      },
      email: localStorage.getItem('volunteer_email'),  // Volunteer email stored in localStorage
      volunteer_id: localStorage.getItem('volunteer_id'),  // Volunteer ID stored in localStorage
      loading: false,  // For showing loading state
      error: '',  // For error messages
      currentLocation: {
        lat: null,
        lon: null,
      },
    };
  },

  methods: {
    // Fetch attendance records by volunteer_id
    async fetchAttendance() {
  this.loading = true;
  this.error = '';

  try {
    const response = await axios.post('http://localhost:3000/volunteer/attendance-by-id', {
      volunteer_id: this.volunteer_id,
    });

    console.log('Response from API:', response.data); // Check the response structure

    if (response.data.status === 'success') {
      this.attendance = response.data.data; // Populate attendance array with response data
    } else {
      this.error = 'No attendance records found.';
    }
  } catch (error) {
    this.error = 'Error fetching attendance. Please try again later.';
  } finally {
    this.loading = false;
  }
},

    // Fetch current location using geolocation API
    getLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            this.currentLocation.lat = position.coords.latitude;
            this.currentLocation.lon = position.coords.longitude;
          },
          () => {
            this.error = 'Unable to retrieve location. Please allow location access.';
          }
        );
      } else {
        this.error = 'Geolocation is not supported by this browser.';
      }
    },

    // Add new attendance record
    async addAttendance() {
      if (!this.currentLocation.lat || !this.currentLocation.lon) {
        this.error = 'Please wait for the location to be retrieved.';
        return;
      }

      const volunteer_id = this.volunteer_id;
      console.log(`Volunteer ID: ${volunteer_id}`);

      // Check if volunteer_id exists
      if (!volunteer_id) {
        this.error = 'Volunteer ID is missing. Please log in again.';
        return;
      }

      // Set current start time and end time (1 hour later)
      const startTime = new Date();
      const endTime = new Date(startTime.getTime() + 60 * 60 * 1000);  // One hour later

      // Helper function to format date for MySQL
      const formatDateToMySQL = (date) => {
        const year = date.getFullYear();
        const month = (date.getMonth() + 1).toString().padStart(2, '0');
        const day = date.getDate().toString().padStart(2, '0');
        const hours = date.getHours().toString().padStart(2, '0');
        const minutes = date.getMinutes().toString().padStart(2, '0');
        const seconds = date.getSeconds().toString().padStart(2, '0');

        return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
      };

      // Format start time and end time
      const formattedStartTime = formatDateToMySQL(startTime);
      const formattedEndTime = formatDateToMySQL(endTime);

      // Prepare attendance data to be sent to the backend
      const attendanceData = {
        volunteer_id: volunteer_id,  // Volunteer ID
        location_latitude: this.currentLocation.lat,
        location_longitude: this.currentLocation.lon,
        start_time: formattedStartTime,
        end_time: formattedEndTime,
      };

      try {
        // Send attendance data to the backend API
        const response = await axios.post('http://localhost:3000/volunteer/add-attendance', attendanceData);

        // Check if the attendance was successfully added
        if (response.data.status === 'success') {
          this.attendance.push(response.data.attendance);  // Add new record to the UI
          this.newAttendance.event_name = '';  // Reset form fields
          this.newAttendance.attendance_status = 'attended';
        } else {
          this.error = 'Failed to add attendance.';
        }
      } catch (error) {
        this.error = 'An error occurred while adding attendance.';
      }
    },
  },

  created() {
    // Fetch attendance records when the component is created
    if (this.volunteer_id) {
      this.fetchAttendance();
      this.getLocation();  // Get the volunteer's location
    } else {
      this.error = 'No volunteer ID found in localStorage. Please log in again.';
    }
  },
};
</script>

<style scoped>
/* Spinner and error styling */
.spinner {
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 2s linear infinite;
  margin: 20px auto;
}

.error {
  color: red;
  font-weight: bold;
  margin-top: 20px;
}

/* Styling for the Add Attendance form */
form {
  margin-top: 20px;
}

form div {
  margin-bottom: 10px;
}

form button {
  background-color: #3498db;
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
}

form button:hover {
  background-color: #2980b9;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
</style>
