<template>
  <div class="profile-container">
    <header>
      <h2>Your Volunteer Profile</h2>
    </header>
    <div class="profile-content">
      <div v-if="profile">
        <div class="profile-item"><strong>First Name:</strong> {{ profile.first_name }}</div>
        <div class="profile-item"><strong>Last Name:</strong> {{ profile.last_name }}</div>
        <div class="profile-item"><strong>Email:</strong> {{ profile.email }}</div>
        <div class="profile-item"><strong>Phone Number:</strong> {{ profile.phone_number }}</div>
      </div>
      <div v-else-if="loading">
        <p>Loading...</p>
      </div>
      <div v-else-if="errorMessage">
        <p class="error-message">{{ errorMessage }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      profile: null,
      loading: true,
      errorMessage: null,
    };
  },
  created() {
    this.fetchProfile();
  },
  methods: {
    async fetchProfile() {
      const email = localStorage.getItem('volunteer_email'); // Retrieve email from localStorage

      if (!email) {
        this.errorMessage = 'Please login first.';
        this.loading = false;
        return;
      }

      try {
        // Send POST request to fetch the profile
        const response = await axios.post('http://localhost:3000/volunteer/profile', { email });

        if (response.data.message === 'Profile retrieved successfully') {
          this.profile = response.data.profile;
        } else {
          this.errorMessage = 'Failed to retrieve profile.';
        }
      } catch (error) {
        console.error('Error fetching profile:', error);
        this.errorMessage = 'An error occurred while fetching the profile.';
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

